* {
    margin: 0px;
    padding: 0px;
  }
  body {
    font-family: sans-serif;
    color: #384c56;
  }
  h3 {
    margin-bottom: 20px;
  }
  button:focus {
    outline: none;
  }
  .todos {
    margin-top: 30px;
    margin-left: 20px;
  }
  .todo-form {
    margin-bottom: 20px;
  }
  .todo-title {
    height: 30px;
    border: 1px solid #dddddd;
    border-radius: 2px;
    letter-spacing: 0.05em;
    padding: 0px 10px;
  }
  .todo-title:focus {
    outline: none;
  }
  .todo-btn-add {
    background: #318fb5;
    border: 1px solid #318fb5;
    color: #cffffe;
    height: 32px;
    width: 45px;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
    position: relative;
    left: -6px;
  }
  .todo-btn-add:focus {
    outline: none;
  }
  .todos-list {
    list-style-type: none;
    padding-left: 0px;
    width: 202px;
  }
  .todos-list li {
    padding: 6px 10px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    border: 1px solid #dddddd;
  }
  .todos-list li span {
    margin-left: 10px;
  }
  .todos-list li.done {
    text-decoration: line-through;
    opacity: 0.5;
  }
  .todos-list li button {
    float: right;
    background: #c26565;
    border: 1px solid #c26565;
    border-radius: 2px;
    color: #ffe0f7;
    height: 17px;
    width: 30px;
    font-size: 0.6em;
  }
  input[type="checkbox"]{
    float: left;
    position: relative;
    top: 2px;
    cursor: pointer;
  }